import { dayIndicators } from './indicators';

function renderGainersTable() {
  const { dayGainers } = dayIndicators;
  const { quotes } = dayGainers;
  const domElements = quotes.map(makeTableline).join('');
  return `
  <ul>
  ${domElements}
  </ul>`;
}

function makeTableline(line) {
  return `<li>
  <a href=""> ${line.symbol} </a>
  <div> ${line.regularMarketPrice} </div>
  <div> ${line.changeInPercent + ' %'}</div>
  <div> ${line.prevClose} </div> 
  </li>`;
  return li;
}

const appRoot = document.querySelector('.app-root');

renderApp();

function renderApp() {
  appRoot.innerHTML = `
  ${startApp()}`;
}

function startApp() {
  return `<section>
        ${renderGainersTable()}
    </section>`;
}
